<template>
  <el-collapse v-model="activeNames" @change="handleChange">
      <el-collapse-item title="南京麻将" name="1">
        <div class="block text-center" style="display: block;width:100%;">
          <el-carousel height="50rem" >
            <el-carousel-item v-for="(image, index) in images" :key="index">
              <img :src="image" alt="carousel image" class="carousel-image" />
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-collapse-item>

      <el-collapse-item title="贵州麻将" name="2">
        <div class="block text-center" style="display: block;width:100%;">
          <el-carousel height="50rem" >
            <el-carousel-item v-for="(image, index) in images1" :key="index">
              <img :src="image" alt="carousel image" class="carousel-image" />
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-collapse-item>

      <el-collapse-item title="四川麻将" name="3">
        <div class="block text-center" style="display: block;width:100%;">
          <el-carousel height="50rem" >
            <el-carousel-item v-for="(image, index) in images2" :key="index">
              <img :src="image" alt="carousel image" class="carousel-image" />
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-collapse-item>
      
      <el-collapse-item title="长沙麻将" name="4">
        <div class="block text-center" style="display: block;width:100%;">
          <el-carousel height="50rem" >
            <el-carousel-item v-for="(image, index) in images3" :key="index">
              <img :src="image" alt="carousel image" class="carousel-image" />
            </el-carousel-item>
          </el-carousel>
        </div>

      </el-collapse-item>
    </el-collapse>








</template>

<script>
import { ref, onMounted } from 'vue'

export default {
  name: 'Wetpants_mj',
  setup() {
    
    const images = ref([])
    const images1 = ref([])
    const images2 = ref([])
    const images3 = ref([])

    const loadImages = () => {
      const requireContext = require.context('@/assets/mj/nj', false, /\.(png|jpe?g|svg)$/)
      const requireContext1 = require.context('@/assets/mj/gz', false, /\.(png|jpe?g|svg)$/)
      const requireContext2 = require.context('@/assets/mj/chuanm', false, /\.(png|jpe?g|svg)$/)
      const requireContext3 = require.context('@/assets/mj/changma', false, /\.(png|jpe?g|svg)$/)
      images.value = requireContext.keys().map(requireContext)
      images1.value = requireContext1.keys().map(requireContext1)
      images2.value = requireContext2.keys().map(requireContext2)
      images3.value = requireContext3.keys().map(requireContext3)
    }

    onMounted(() => {
      loadImages()
    })

    return {
      images,
      images1,
      images2,
      images3
    }
  }
}
</script>

<style scoped>
.carousel-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain; /* 保持图片的纵横比，同时使图片完全适应容器 */
}
</style>